<template>
  <div>
    <v-app-bar color="#2195F2" dark app>
      <v-app-bar-nav-icon @click="back">
        <v-icon>{{icon}}</v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>{{title}}</v-toolbar-title>
      <div class="flex-grow-1"></div>
      <v-btn to="/" icon>
        <v-icon>mdi-home</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
      <v-menu left bottom>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <AppbarMenus v-on:menuDialog-show="listenDialog" />
      </v-menu>
    </v-app-bar>

    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline">{{dialogData.title}}</v-card-title>
        <v-card-text>{{dialogData.content}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import AppbarMenus from "@/components/appbar-menus.vue";
export default {
  components: {
    AppbarMenus
  },
  props: {
    title: {
      default: ""
    },
    icon: {
      default: "mdi-menu"
    }
  },
  data() {
    return {
      dialog: false,
      selecedTabTitle: "",
      dialogData: {
        title: "",
        content: ""
      }
    };
  },
  mounted() {
    //this.selecedTabTitle=this.vdata.language[0];
  },
  methods: {
    tabClick(item) {
      console.info(item);
      this.selecedTabTitle = item;
    },
    back() {
      if (this.title != "") {
        this.$router.back(-1);
      } else {
        this.$router.push("/custom");
      }
    },
    listenDialog(data) {
      this.dialog = true;
      this.dialogData = data;
    }
  }
};
</script>