<template>
  <div>
    <v-content>
      <v-container class="pa-2" fluid>
        <Appbar :title="design.title" :icon="icon" />

        <!-- <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="headline">{{design.title}}</v-list-item-title>
            <v-list-item-subtitle>{{design.subtitle}}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        -->

        <!-- <v-card-text>
          <span class="orange--text subtitle-1">:{{design.price}}VB</span>
          <br />
          <span>+</span>
          <v-chip-group column>
            <v-chip v-for="(itemTag,index) in design.tags" :key="index">{{itemTag}}</v-chip>
          </v-chip-group>
        </v-card-text>

        <v-card-actions>
          <v-btn color="error"></v-btn>
          <v-btn color="primary"></v-btn>
          <v-btn color="warning"></v-btn>
        </v-card-actions>-->

        <v-card>
          <v-carousel vertical-delimiters>
            <v-carousel-item v-for="(item,i) in design.images" :key="i">
              <v-img height="500" contain :src="item"></v-img>
            </v-carousel-item>
          </v-carousel>

          <v-card-title>{{design.title}}</v-card-title>
          <v-card-text>
            <v-row align="center">
              <div class="grey--text ml-4">{{design.subtitle}}</div>
            </v-row>
            <v-row align="center">
              <div class="grey--text ml-4">{{design.document?"+":""}}</div>
            </v-row>
            <div class="my-4 subtitle-1 red--text">{{design.price}}VB</div>
          </v-card-text>

          <v-divider class="mx-4"></v-divider>

          <v-card-text>
            <div class="title text--primary"></div>
            <v-chip-group column>
              <v-chip v-for="(itemTag,index) in design.tags" :key="index">{{itemTag}}</v-chip>
            </v-chip-group>
          </v-card-text>

          <v-card-actions>
            <v-btn color="blue" @click="chat" text></v-btn>
            <v-btn color="red" @click="buy" text></v-btn>
          </v-card-actions>

          <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header></v-expansion-panel-header>
              <v-expansion-panel-content>
                <!-- <vue-markdown>{{design.content}}</vue-markdown> -->

                <mavon-editor
                  boxShadowStyle="0"
                  :toolbarsFlag="false"
                  defaultOpen="preview"
                  :subfield="false"
                  v-model="design.content"
                />
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline"></v-card-title>
        <v-card-text>{{dialogTitle}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            v-clipboard:copy="1224657851"
            @click="dialog = false"
          >QQ</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>

  <!-- <video style="width: 100%;object-fit: fill;" src="https://vaemc-design.oss-cn-shenzhen.aliyuncs.com/iot/5da04646fb6fae980799ba68/VID_20190625_190650.mp4" controls="controls"/> -->
</template>
<script>
// import VueMarkdown from "vue-markdown";
import Appbar from "@/components/appbar.vue";

import { ObjectID } from "bson";
export default {
  components: {
    Appbar
  },
  data() {
    return {
      dialog: false,
      dialogTitle:"",
      title: "",
      icon: "mdi-arrow-left",
      colors: ["primary", "secondary", "yellow darken-2", "red", "orange"],
      design: {}
    };
  },
  mounted() {
    //this.tabsVisible = false;

    if (ObjectID.isValid(this.$route.params.id)) {
      this.axios
        .get("design/" + this.$route.params.id)
        .then(response => {
          this.design = response.data;
          //console.info(this.design=="false");

          if (!this.design) {
            this.$router.push("/");
          }
        })
        .catch(response => {
          console.log(response);
        });
    } else {
      this.$router.push("/");
    }
  },
  methods: {
    buy() {
      this.dialogTitle="QQ 1224657851 ";
      this.dialog = true;
    },
    chat() {
      this.dialogTitle="QQ 1224657851 ";
       this.dialog = true;
    }
  }
};
</script>
