<template>
  <div>
    <v-list>
      <v-list-item v-for="n in barsMenu" :key="n" @click="barsMenuClick(n)">
        <v-list-item-title>{{ n }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </div>
</template>
<script>
export default {
  data() {
    return {
      barsMenu: ["", ""]
    };
  },
  methods: {
    barsMenuClick(n) {
      switch (n) {
        case "":
          let dialogData = {
            title: "VaemcDesign",
            content: "QQ1224657851"
          };
          this.$emit("menuDialog-show", dialogData);
          break;
        case "":
           this.$router.push("/custom");
          break;
      }
    }
  }
};
</script>